        align-content: start;
    }
    .contact-hero__item-1{
        grid-column: 2 / 5;
        padding: 100px 0;
        padding-left: 20px;
    }
    .contact-hero__title{
        line-height: 5.5rem;
        font-size: 6rem;
        margin-bottom: 50px;
    }
    .contact-hero__copyright{
        font-size: 0.7rem;
        line-height: 0.8rem;
        font-weight: 500;
        animation: opacity 1.2s 1 ease-in-out forwards;
    }
