            z-index: 1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #f4f4f4;
            animation: hero-img-animation 0.8s 1 0.4s ease-in-out forwards;
        }
    .contact-hero__item-3{
        grid-column: 2 / 3;
        grid-row: 2 / 5;
        padding-left: 10px;
    }
    .contact-hero__decoration{
