        font-weight: 700;
        margin-bottom: 20px;
        animation: opacity 1.2s 1 ease-in-out forwards;
    }
    .contact-hero__mail{
        color: #252422;
    }
    .contact-hero__mail:hover{
            text-decoration: none;
        }
    .contact-hero__mail{
        animation: opacity 1.2s 1 ease-in-out forwards;
}
    .contact-hero__link{
        display: inline-block;
        text-decoration: none;
        border-radius: 20px;
