___CSS_LOADER_EXPORT___.push([module.id, `.services{
    width: 100%;
    height: auto;
}
    .services__container{
        width: 90%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        padding: 20px 0;
        border-top: #252422 solid 2px;
        position: relative;
    }
    .services__page-numbers{
        width: 32%;
        position: absolute;
        top: 20px;
        left: 0;
        display: flex;
        justify-content: space-between;
        font-weight: 500;
        font-size: 1.2rem;
    }
    .services__content{
        width: 33%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        padding-right: 60px
    }
    .services__link{
        display: inline-block;
        text-decoration: none;
        color: #252422;
        border: #252422 1px solid;
        border-radius: 20px;
        padding: 8px 30px;
        font-size: 0.8rem;
        font-weight: 600;
        background-image: linear-gradient(to top, #252422 50%, #f4f4f4 50%);
        background-size: 100% 200%;
    }
    .services__link:hover{
            animation: button-animation 0.25s 1 ease-in-out forwards;
            
        }
    .services__link:not(hover){
            animation: button-animation-off 0.25s 1 ease-in-out forwards;
        }
    .services__title{
        font-size: 2.5rem;
        font-weight: 700;
        line-height: 2.5rem;
    }
    .services__text{
        margin: 30px 0;
        font-weight: 500;
        font-size: 1.2rem;
    }
    .services__card-container{
        width: 67%;
        display: flex;
        justify-content: space-between;
        border-left: #252422 solid 2px;
    }
    .services__card{
        margin-left: 20px;
        width: 48%;
        text-decoration: none;
        color: #252422;
        text-transform: uppercase;
    }
    .services__card:hover .services__card-title{
            -webkit-text-decoration: #252422 underline 1.5px;
                    text-decoration: #252422 underline 1.5px;
        }
    .services__card:hover .services__media{
            transform: scale(1.05);
            transition: all 0.2s ease-in-out;
        }
    .services__card:not(hover) .services__media{
            transform: scale(1);
            transition: all 0.2s ease-in-out;
        }
    .services__media-container{
        width: 100%;
        overflow: hidden;
    }
    .services__media{
        width: 100%;
        -o-object-fit: cover;
           object-fit: cover;
        
    }
    .services__card-title{
        margin: 10px 20px;
        font-size: 1.3em;
    }
    .services__card-text{
        margin: 0 20px;
        width: 60%;
        font-size: 0.9em;
        font-weight: 500;
    }




@media screen and (max-width: 1024px) {
        .services__container{
            flex-direction: column;
        }
        .services__page-numbers{
            width: 100%;
            font-weight: 400;
            font-size: 1rem;
        }
        .services__content{
            width: 100%;
            padding-right: 400px;
            margin: 50px 0;
            
        }
        .services__title{
            font-size: 1.5rem;
            line-height: 1.5rem;
        }
        .services__text{
            font-size: 1rem;
            font-weight: 400;
        }
        .services__card-container{
            width: 100%;
            border-left: none;
        }
        .services__card{
            margin-left: 0;
            width: 50%;
            margin-right: 15px;
            padding-bottom: 15px;
        }
        .services__media-container{
            width: 100%;
        }
        .services__card-title{
            margin: 10px;
            font-size: 1.1em;
        }
        .services__card-text{
            width: 40%;
            margin: 0 10px;
            font-size: 0.7em;
        }
}

@media screen and (max-width: 500px) {
        
        .services__content{
            padding-right: 200px;
        }
        .services__card-container{
            overflow: auto;
        }
        .services__card{
            width: 350px;
        }
        .services__media-container{
            width: 350px;
        }
