.about-hero{
    width: 100%;
    padding-top: 200px;
}
    .about-hero__container{
        width: 90%;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 50% 50%;
        grid-template-rows: 25% 5% 65% 5%;
        justify-content: space-between;
        align-content: start;
    }
    .about-hero__item-1{
        grid-column: 1/2;
        grid-row: 1/2;
    }
    .about-hero__item-2{
        grid-column: 2/3;
        grid-row: 2/4;
    }
    .about-hero__item-3{
        grid-column: 1/2;
        grid-row: 4/5;
    }
    .about-hero__title{
        font-size: 6rem;
        line-height: 5.5rem;
    }
    .about-hero__paragraph{
        font-size: 2.5rem;
        line-height: 2.5rem;
        margin-top: 40px;
        animation: opacity 1.5s 1 ease-in-out forwards;
    }
    .about-hero__logo{
        fill: #f9cdcd;
        width: 40px;
    }



.about-images{
    width: 100%;
}



.about-images__container{
        width: 90%;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 25% 75%;
        justify-content: space-between;
        align-content: start;
        padding-top: 200px;
        border-top: #f9cdcd80 solid 2px;
    }



.about-images__item-1{
        grid-column: 1/2;
    }



.about-images__item-2{
        grid-column: 2/3;
        position: relative;
    }



.about-images__copyright{
        font-size: 0.8rem;
        font-weight: 500;
        line-height: 0.8rem;
        margin: 700px 0;
    }



.about-images__img{
        width: 55%;
        -o-object-fit: cover;
           object-fit: cover;
        position: absolute;
    }



.about-images__img-1{
        z-index: 3;
        top: 0;
        left: 0;
    }



.about-images__img-1:hover{
            z-index: 5;
        }



.about-images__img-2{
        z-index: 4;
        top: 20%;
        left: 10%;
    }



.about-images__img-2:hover{
            z-index: 5;
        }



.about-images__img-3{
        z-index: 2;
        top: 10%;
        left: 20%;
    }



.about-images__img-3:hover{
            z-index: 5;
        }



.about-images__img-4{
        z-index: 1;
        top: 30%;
        left: 45%;
    }



.about-images__img-4:hover{
            z-index: 5;
        }



.about-logo{
    width: 100%;
}



.about-logo__container{
        width: 90%;
        margin: 0 auto;
        border-top: #f9cdcd80 solid 2px;
    }



.about-logo__svg{
        fill: #f9cdcd;
        width: 80%;
        margin: 20px 0;
    }





@media screen and (max-width: 1024px) {
    .about-hero{
        padding-top: 150px;
    }
        .about-hero__container{
            grid-template-columns: 100%;
            grid-template-rows: auto auto auto auto;
        }
        .about-hero__item-1{
            grid-column: 1/2;
            grid-row: 1/2;
        }
        .about-hero__item-2{
            grid-column: 1/2;
            grid-row: 2/3;
        }
        .about-hero__item-3{
            grid-column: 1/2;
            grid-row: 3/4;
        }
        .about-hero__title{
            font-size: 4rem;
            line-height: 4.2rem;
        }
        .about-hero__paragraph{
            font-size: 2rem;
            line-height: 2rem;
            margin: 40px 0;
        }
        .about-hero__logo{
            fill: #f9cdcd;
            width: 40px;
        }
        
        .about-images__container{
            grid-template-columns: 100%;
