        }
        .about-images__copyright{
            font-size: 0.7rem;
            line-height: 0.7rem;
            margin: 0;
            margin-bottom: 100px;
        }
        .about-logo__svg{
            width: 100%;
        }
}

@media screen and (max-width: 500px) {

