ound-color: #f4f4f4;\r\n    transition: background-color 1s;\r\n}\r\n.appear{\r\n    color: #f9cdcd;\r\n    background-color: #252422;\r\n}\r\n\r\n.hero-animation-con{\r\n    display: inline-block;\r\n    overflow: hidden;\r\n    height: 70px;\r\n}\r\n.hero-animation{\r\n    display: inline-block;\r\n    position: relative;\r\n    margin-top: 70px;\r\n    animation: hero-animation 0.5s 1 ease-in-out forwards;\r\n    &--delay-1{\r\n        animation-delay: 0.2s;\r\n    }\r\n    &--delay-2{\r\n        animation-delay: 0.4s;\r\n    }\r\n    &--delay-3{\r\n        animation-delay: 0.6s;\r\n    }\r\n    &--delay-4{\r\n        animation-delay: 0.8s;\r\n    }\r\n    &--delay-5{\r\n        animation-delay: 1s;\r\n    }\r\n}\r\n\r\n\r\n\r\n.header{\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: 10;\r\n    width: 100%;\r\n    height: 130px;\r\n    &--black{\r\n        background-color: #252422;\r\n    }\r\n    &--white{\r\n        background-color: #f4f4f4;\r\n    }\r\n    .header__container{\r\n        width: 90%;\r\n        margin: 0 auto;\r\n        height: 100%;\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: space-between;\r\n        position: relative;\r\n    }\r\n    .header__logo{\r\n        width: 160px;\r\n        fill: #f4f4f4;\r\n        &--black{\r\n            fill: #252422;\r\n        }\r\n        &--pink{\r\n            fill: #f9cdcd;\r\n        }\r\n    }\r\n    .header__nav{\r\n        display: flex;\r\n        padding-right: 100px;\r\n    }\r\n    .header__nav-item{\r\n        color: #f4f4f4;\r\n        text-decoration: none;\r\n        margin: 0 2rem;\r\n        position: relative;\r\n        &:after{\r\n            height: 1px;\r\n            position: absolute;\r\n            content: '';\r\n            bottom: 0;\r\n            left: 0;\r\n            background-color: #f4f4f4;\r\n            width: 0;\r\n            transition: all 0.35s ease;\r\n        }\r\n        &:hover:after{\r\n      