___CSS_LOADER_EXPORT___.push([module.id, `.careers-hero{
    width: 100%;
    background-color: #252422;
    color: #f9cdcd;
}
    .careers-hero__container{
        width: 90%;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 50% 50%;
        grid-template-rows: 35% 5% 25% 25% 10%;
        justify-content: space-between;
        align-content: start;
    }
    .careers-hero__item-1{
        grid-column: 1/2;
        grid-row: 1 / 2;
        padding: 250px 0;
    }
    .careers-hero__title{
        line-height: 5.5rem;
        font-size: 6rem;
    }
    .careers-hero__item-2{
        grid-column: 2/3;
        grid-row: 1 / 4;
        padding: 150px 0;
    }
    .careers-hero__item-3{
        grid-column: 1/2;
        grid-row: 2/3;
    }
    .careers-hero__card{
        width: 100%;
        height: 130vh;
        position: relative;
    }
    .careers-hero__card-img{
            width: 100%;
            height: 100%;
            -o-object-fit: cover;
               object-fit: cover;
        }
    .careers-hero__card-div{
            position: absolute;
            z-index: 1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #252422;
            animation: hero-img-animation 0.8s 1 0.8s ease-in-out forwards;
        }
    .careers-hero__card-sub{
            display: inline-block;
            margin: 10px 200px 10px 0;
        }
    .careers-hero__copyright{
        font-size: 0.7rem;
        line-height: 0.8rem;
        font-weight: 500;
    }
    .careers-hero__item-4{
        grid-column: 2 / 3;
        grid-row: 4 / 6;
        margin-bottom: 400px;
    }
    .careers-hero__content{
        padding-right: 100px;
        position: relative;
    }
    .careers-hero__content:after{
            content: '';
            position: absolute;
            right: 0;
            top: 10px;
            width: 25px;
            height: 25px;
            border-radius: 50%;
            background-color: #f9cdcd;
        }
    .careers-hero__paragraph{
        font-size: 2.3rem;
        line-height: 2.6rem;
        font-weight: 500;
    }
    .careers-hero__link{
        display: inline-block;
        text-decoration: none;
        border-radius: 20px;
        padding: 8px 30px;
        margin-top: 40px;
        font-size: 0.8rem;
        font-weight: 600;
        background-size: 100% 200%;
    }
    .careers-hero__link--black{
            color: #252422;
            border: #252422 1px solid;
            background-image: linear-gradient(to top, #252422 50%, #f4f4f4 50%); 
        }
    .careers-hero__link--black:hover{
                animation: button-animation 0.25s 1 ease-in-out forwards;
                
            }
    .careers-hero__link--black:not(hover){
                animation: button-animation-off 0.25s 1 ease-in-out forwards;
            }
    .careers-hero__link--pink{
            color: #f9cdcd;
            border: #f9cdcd 1px solid;
            background-image: linear-gradient(to top, #f9cdcd 50%, #252422 50%); 
        }
    .careers-hero__link--pink:hover{
                animation: button-animation-pink 0.25s 1 ease-in-out forwards;
                
            }
    .careers-hero__link--pink:not(hover){
                animation: button-animation-pink-off 0.25s 1 ease-in-out forwards;
            }



@media screen and (max-width: 1024px) {   
        .careers-hero__container{
        grid-template-columns: 100%;
        grid-template-rows: auto auto auto auto;
        }
        .careers-hero__item-1{
            grid-column: 1/2;
            grid-row: 1 / 2;
            padding-top: 150px;
            padding-bottom: 50px;
        }
        .careers-hero__title{
            line-height: 4rem;
            font-size: 4rem;
        }
        .careers-hero__item-2{
            grid-column: 1/2;
            grid-row: 3/4;
            padding: 50px 0;
        }
        .careers-hero__item-3{
            grid-column: 1/2;
            grid-row: 2/3;
        }
        .careers-hero__item-4{
            grid-column: 1/2;
            grid-row: 4/5;
            margin-bottom: 100px;
        }
        .careers-hero__card{
            height: 90vh;
        }
        .careers-hero__content{
            margin-top: 50px;
            padding: 0;
        }
            .careers-hero__content:after{
                display: none;
            }
        .careers-hero__paragraph{
            font-size: 2rem;
            line-height: 2.3rem;
        }
}

@media screen and (max-width: 500px) {
        .careers-hero__title{
            line-height: 2rem;
            font-size: 3rem;
        }
}




@keyframes hero-img-animation{
    0%{
        height: 100%;
    }
    100%{
        height: 0;
    }
}

